.container-gallery {
    width: 100%;
    height: 805px;
    background-color: primary-background-home;
    border-radius: 25px;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 4px 0 20px 0;

    @include desktop {
        background-color: $primary-background-grey-light;
        margin: 20px 0 43px 0;
        height: 829px;
    }
}

.content-gallery {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 0;
    @include tablette-only{
        justify-content: space-between; 
    }

    @include desktop {
        padding: 25px 45px 25px 45px;
    }
}