.bandeau-container {
    width: 100%;
    height: auto;
}

.bandeau-content {
    display: flex;
    justify-content: left;
    align-items: center;
    position: relative;
    padding-top: 8px;
    padding-bottom: 16px;

    @include desktop {
        padding-top: 23px;
        padding-bottom: 31px;
        justify-content: center;
    }
}

.bandeau-content__img {
    width: 100%;
    object-position: 50% 50%;
    object-fit: cover;
    height: 111px;
    border-radius: 10px;
    -webkit-filter: brightness(70%);
    filter: brightness(70%);

    @include tablette-only {
        border-radius: 25px;
        height: 180px;
    }

    @include desktop {
        height: 223px;
    }
}

.bandeau-content__text {
    display: block;
    position: absolute;
    width: 80%;
    padding-left: 20px;
    color: $color-white;

    @include tablette-only {
        width: auto;
    }
}